@use "../util" as *;

.carriere {
  img {
    width: 100%;
  }
  &-header {
    background-image: linear-gradient(
        hsla(29, 85%, 35%, 0.9),
        hsla(29, 85%, 35%, 0.9)
      ),
      url("https://res.cloudinary.com/digital-x-sarl/image/upload/v1668417980/dkfarm/accueil/carriere/carrierebg.png");
    color: var(--white);
    &--desc {
      padding-top: rem(150);
      text-align: center;
    }
    &--img {
      display: flex;
      align-items: flex-end;
      .profil {
        margin-left: -4rem;
        @include breakpoint-down(medium) {
          margin-left: -2rem;
        }
        @include breakpoint-down(small) {
          margin-left: -1rem;
        }
        @include breakpoint-down(xxsmall) {
          margin-left: -0.3rem;
        }
      }
      .profils {
        margin-right: -4rem;
        @include breakpoint-down(medium) {
          margin-right: -2rem;
        }
        @include breakpoint-down(small) {
          margin-right: -1rem;
        }
        @include breakpoint-down(xxsmall) {
          margin-right: -0.3rem;
        }
      }
      .profilss {
        margin-left: -7rem;
        @include breakpoint-down(medium) {
          margin-left: -4rem;
        }
        @include breakpoint-down(small) {
          margin-left: -1rem;
        }
      }
    }
  }

  &-blocs {
    gap: rem(40) rem(10);
    padding-top: rem(32);
    @include breakpoint-down(medium) {
      grid-template-columns: repeat(3, 1fr);
    }
    @include breakpoint-down(small) {
      grid-template-columns: repeat(2, 1fr);
    }
    @include breakpoint-down(xxsmall) {
      grid-template-columns: repeat(1, 1fr);
    }

    &--img {
      display: block;
      position: relative;
      width: 100%;
      height: 30vw;
      background-position: center;
      background-repeat: no-repeat;
      background-size: cover;
      overflow: hidden;
      @include breakpoint(xlarge){
        height: 20vw;
      }
      @include breakpoint-down(tablet) {
        height: 40vw;
      }
      @include breakpoint-down(small) {
        height: 100vw;
      }
      &:hover .carriere-blocs--overlay {
        background-color: var(--brown--lighter);
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
      }
    }
    &--overlay {
      color: var(--white);
      text-align: center;
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 0;
      transition: all 400ms;
      .paragraph {
        font-weight: 300;
      }
      h4 {
        font-weight: 900;
      }
      .btn {
        align-items: center;
        justify-content: center;
        color: var(--white);
        padding-top: rem(32);
        cursor: pointer;
      }
    }
  }
}
.image {
  &Pop {
    width: 100%;
    position: relative;
    margin-top: 2rem;
    z-index: 200;
    display: none;
    &Under {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 12;
      &:hover {
        cursor: pointer;
      }
    }
    .close {
      position: absolute;
      z-index: 14;
      top: 5%;
      right: 5%;
      cursor: pointer;
      color: var(--black);
    }
    .blocks {
      align-content: center;
      @include breakpoint-down(tablet) {
        grid-template-columns: repeat(1, 1fr);
      }
    }
  }
  &Contain {
    position: relative;
    top: 0;
    left: -20%;
    height: auto;
    z-index: 12;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    cursor: pointer;
    @include breakpoint-down(tablet) {
      height: 100vw;
    }
  }
  &Text {
    background-color: var(--pink-light);
    padding: 0 rem(10) 0 rem(48);
    display: flex;
    flex-direction: column;
    @include breakpoint-down(tablet){
      padding: rem(16);
    }
    .flex {
      display: flex;
      flex-direction: column;
      .title {
        text-align: initial;
        font-size: rem(24);
      }
      .paragraph {
        padding-top: rem(17);
      }
      &bt {
        padding: rem(50) 0;
        @include breakpoint-down(tablet) {
          padding: rem(25) 0;
        }
      }
      &tp {
        padding-top: rem(50);
        @include breakpoint-down(tablet) {
          padding-top: rem(25);
        }
      }
    }
  }
}

@keyframes he {
  100% {
    left: 0;
  }
}
